import { Component, OnInit } from '@angular/core';
import { Base64 } from 'js-base64';

@Component({
  standalone: false,
  selector: 'app-base64',
  templateUrl: './base64.component.html',
  styleUrls: ['./base64.component.scss']
})
export class Base64Component implements OnInit {
  out1 = '';
  out2 = '';

  constructor() { }

  ngOnInit(): void {
  }

  encode() {
    this.out2 = Base64.encode(this.out1, true)
  }

  decode() {
    this.out2 = Base64.decode(this.out1)
  }

  swap() {
    const temp = this.out1
    this.out1 = this.out2
    this.out2 = temp
  }
}
